<template>
  <div>
    <h1>Index of {{decodeURIComponent(path)}}</h1>
    <div>
      <p>
        <label style="border: 1px solid black;display: inline-block;">
          上传文件
          <input
            @change="uploadFile"
            type="file"
            name="file"
            hidden
          >
        </label>
      </p>

      <form
        id="form"
        action="/api/uploadDirectory"
        method="post"
        enctype="multipart/form-data"
        @change="uploadDirectory"
      >
        <label style="border: 1px solid black;display: inline-block;">
          上传文件夹
          <input
            type="file"
            name="folder"
            multiple
            webkitdirectory
            hidden
          />
        </label>
        <input
          name="path"
          :value="path"
          type="hidden"
        />
      </form>
    </div>
    <hr>
    <div>
      <div style="width: 300px">
        <a :href="'/#'+parentPath+''">../</a>
      </div>
      <div
        v-for="file in files"
        :key="file.fileName"
      >
        <div style="display: inline-block;width: 300px">
          <a
            v-if="file.fileType == 'DIRECTORY'"
            :href="'#'+path+encodeURIComponent(file.fileName)+'/'"
          >{{file.fileName}}/</a>
          <a
            v-if="file.fileType == 'FILE'"
            :href="previewPath + file.relativePath+ file.fileName"
          >{{file.fileName}}</a>
        </div>
        <div style="display: inline-block;width: 200px">
          {{file.createTime}}
        </div>
        <div style="display: inline-block">
          {{file.size}}
        </div>
      </div>

    </div>
    <hr>
  </div>
</template>

<script>

import { list } from '@/api/list'

export default {
  name: 'Index',
  data () {
    return {
      path: "/",
      previewPath: '',
      parentPath: '/',
      files: [

      ]
    }
  },
  mounted () {

   this.pathUpdate(this.$route)
  },
  methods: {
    //数据请求
    listPath (path) {
      let _self = this
      list(path).then(function (response) {
        if (response.data.code == 20000) {
          _self.previewPath = response.data.data.previewPath
          _self.files = response.data.data.files
        } else {
          alert(response.data.showMsg)
        }
      })
    },
    updatePathData () {
      this.listPath(this.path)
    },
    uploadFile (e) {
      const formdata = new FormData();
      //当用户选择文件时
      var file = e.target.files[0]; //假设单个文件选择
      if (!file) return; //如果没有文件，不做任何事情
      var req = new XMLHttpRequest(); //创建新请求
      formdata.append("path", this.path);
      formdata.append('file', file);
      req.open("POST", "/api/uploadFile"); //向这个URL发送POST请求
      req.send(formdata); //把文件作为主体发送
      req.onload = function () {
        window.location.reload();
      }
    },
    uploadDirectory(e){
      e.path[2].submit()
    },
    calcParentPath(path){
      return path.substr(0,path.substr(0,path.length-1).lastIndexOf("/"))+"/"
    },
    pathUpdate(to){
       this.path = to.path;
      //计算parentPath
      this.parentPath=this.calcParentPath(this.path)
      this.updatePathData()
    }
  },
  watch: {
    '$route' (to) {
      // 对路由变化作出响应...
     this.pathUpdate(to)
     
    }
  },

}
</script>

